.app {
  display: flex;
  flex-direction: column;
  text-align: center;
}

@media screen and (max-width: 415px) {
  .app .pet-frame-light {
    overflow: scroll;
    width: auto;
  }

  .container {
    flex-direction: column;
    width: auto;
  }

  .pet-name {
    font-size: 5rem;
  }
}


.h1-header {
  color: rgb(195, 149, 255)
}

.container {
  margin: 1rem auto;
  display: inline-block;
  justify-content: center;
  align-items: center;
  gap: 0.2rem;
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 85%;
}

.pet-pic {
  object-fit: contain;
  width: 100%;
  height: 100%;
  scale: 180%
}

.pet-name {
  color: #d54d7b;
  font-family: "Great Vibes", cursive;
  font-size: 2rem;
  font-weight: normal;
  margin-bottom: 1rem;
  margin-top: 1rem;
  text-align: center;
  text-shadow: 0 1px 1px #fff;
}

.pet-status {
  padding: 0.5rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.pet-frame-light {
  margin: auto;
  width: 15rem;
  height: 7rem;
  align-items: center;
  display: flex;
  align-content: center;
  justify-content: center;
  background-color: rgba(223, 220, 192, 0.514);
  border: 1px solid aliceblue;
  border-radius: 2rem;
  box-shadow: 2px black;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 3rem;
  transition: 0.8s all;
}

.pet-frame-dark {
  background-color: rgba(10, 8, 8, 0.452);
}

.button {
  float: left;
  width: 5rem;
  height: 5rem;
  background-color: rgba(118, 74, 136, 0.562);
  color: aliceblue;
  align-items: center;
  border: 1px solid aliceblue;
  font-size: smaller;
  display: flex;
  align-content: center;
  justify-content: center;
  border-radius: 8px;
  box-shadow: 2px black;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transition: 0.2s all;
  cursor: pointer;
  margin: 0.2rem;
}

.button:hover {
  background-color: rgba(178, 169, 182, 0.562);
}

.button:active {
  transform: scale(0.87);
  /* Scaling button to 0.98 to its original size */
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  /* Lowering the shadow */
}

.button-pet-input {
  width: 10rem;
  height: 1.5rem;
  background-color: rgba(118, 74, 136, 0.562);
  color: aliceblue;
  align-items: center;
  border: 1px solid aliceblue;
  font-size: smaller;
  /* display: flex; */
  align-content: center;
  justify-content: center;
  border-radius: 8px;
  box-shadow: 2px black;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  margin-left: 5px;
}

.button-pet-input:hover {
  background-color: rgba(178, 169, 182, 0.562);
}

.button-pet-input:active {
  transform: scale(0.87);
  /* Scaling button to 0.98 to its original size */
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  /* Lowering the shadow */
}

.hidden {
  margin: auto;
  display: none;
}

.shown {
  display: flex;
}